#page-body{
    min-height: calc(100vh - 75px)
}

.dark-color {
    background-color: #282c34;
    padding: 0px 10rem;
    height: 75px;
}

.navbar-title {
    color: #61dafb;
    font-weight: bold;
    font-size: 20px;
    transition: 0.5s;
}

.navbar-title:hover{
    color: #009cc7;
    transition: 0.5s;
}

.text-align-center{
    text-align: center;
}

.header{
    padding: 3.5rem 25%;
    background: #dff8ff;
    color: #282c34;
    border-bottom: 1px solid #87eaff;
}

.container{
    padding: 2.5rem;
}

.form-group-question{
    margin-left: 20% !important;
    margin-right: 20% !important;
}

.custom-pill{
    margin: 5px;
    font-size: 80%;
    background-color: #eaeaea;
}

.slot-image {
    width: 100%;
    background: white;
    border: 1px solid gainsboro;
    padding: 1rem;
    border-radius: 15px;
    box-shadow: 3px 2px 12px 2px #d0d0d0;
}

.results-paragraph{
    margin-bottom: 5px;
    margin-left: 20px;
    font-size: 12px;
}

.underline{
    text-decoration: underline;
}

.border{
    border: 1px solid gainsboro;
    border-radius: 25px;
    box-shadow: 3px 1px 12px 0px #b9b9b9;
}

.homepage{
    height: calc(100vh - 75px);
    background: #dff8ff;
    color: #282c34;
}